import projectData from "./projectData";

export default function Projects() {
  return (
    <div className="Services">
      <h2 className="text-center">Projects</h2>
      <div className="row mt-5">
        {projectData.map((e) => {
          return (
            <div className="col-md-3 col-sm-10 col-xsm-10 py-2 projectcard">
              <div class="card">
                <img src={e.project_image} class="card-img-top" alt="..." />
                <div class="card-body">
                  <h4 class="card-text text-center">
                    <a href={e.project_link} target="_blank">
                      {e.project_name}
                    </a>
                  </h4>
                </div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
